<template>
	<div id="main">
	<div class="block"></div>
	   <div class="container">
         <titleNavigation titleName="国学智慧教室(馆)"></titleNavigation>
         <div class="center">
         	<p>达康书记的框架奥斯卡大家开始看电视看得见刷卡机打卡机打卡机科大手机打开撒娇的空间撒肯德基奥斯卡达拉斯空当接龙卡萨达萨达来得及垃圾垃圾断开了的拉萨搭建大空间</p>
         	<p>大水坑杰卡斯积分卡死灵法师就爱看福建省客服即可萨芬卡是否卡是否卡设计费卡死了发链接发卡机发卡量饭卡积分卡设计费卡了发甲方将阿里</p>
         </div>
	   </div>
	   <div class="block"></div>
	   <leftNav></leftNav>
	</div>
</template>

<script>
import leftNav from '@/components/leftNav'
import titleNavigation from '@/components/titleNavigation'
 export default{
 	name:'index',
 	components: {
 	  leftNav,
 	  titleNavigation,
 	},

 }
</script>

<style scoped>
	#main{
		width:100%;
		background:#f1f1f1;
	}
	.block{
		width:100%;
		height:20px;
		background:#f1f1f1;
	}
	.container{
		width:1200px;
		margin:0 auto;
		background:#fff;
	}
	.center{
		padding:30px 176px;
	}
	.center p{
		font-size:14px;
		color:#595959;
		letter-spacing:2px;
		line-height:24px;
		text-indent:2em;
	}
</style>


